/** ==============================
 *  Main container styles.
 *  =============================*/
.main {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	background: -webkit-gradient(linear, left top, right top, color-stop(5%, rgb(230, 230, 230)), color-stop(10%, rgb(242, 242, 242)), color-stop(55%, rgb(255, 255, 255)), color-stop(95%, rgb(242, 242, 242)), to(rgb(230, 230, 230)));
	background: -webkit-linear-gradient(left, rgb(230, 230, 230) 5%, rgb(242, 242, 242) 10%, rgb(255, 255, 255) 55%, rgb(242, 242, 242) 95%, rgb(230, 230, 230) 100%);
	background: -o-linear-gradient(left, rgb(230, 230, 230) 5%, rgb(242, 242, 242) 10%, rgb(255, 255, 255) 55%, rgb(242, 242, 242) 95%, rgb(230, 230, 230) 100%);
	background: linear-gradient(90deg, rgb(230, 230, 230) 5%, rgb(242, 242, 242) 10%, rgb(255, 255, 255) 55%, rgb(242, 242, 242) 95%, rgb(230, 230, 230) 100%);
}

.main a {
	position: relative;
	text-decoration: none;
	color: #006080;
	-webkit-transition: color 0.25s linear;
	-o-transition: color 0.25s linear;
	transition: color 0.25s linear;
}

.main a:hover, .main a:focus {
	color: #0099cc;
}

.main a::after {
	border-bottom: 1px solid #0099cc;
}

/* Home page top styles */
.home_top {
	background-image: url(../images/home_bg.jpg);
	background-size: cover;
	background-position: center;
	width: 100%;
	min-height: 50vh;
	border-bottom: 1px solid grey;
	-webkit-box-shadow: 1px 2px 10px 1px grey;
	        box-shadow: 1px 2px 10px 1px grey;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
}

.home_top_container {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	max-width: 980px;
	margin: 0 auto;
	position: relative;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.home_top_content {
	margin: 1em 1em 4em; /* 4em at the bottom for the bullets space */
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.2)), color-stop(10%, rgba(255, 255, 255, 0.5)), color-stop(rgba(255, 255, 255, 0.7)), color-stop(90%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.2)));
	background: -webkit-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: -o-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	border-radius: 26px;
	color: black;
	border: 1px solid deepskyblue;
	border-color: transparent lightyellow lightyellow transparent;
	-webkit-box-shadow: 4px 4px 15px 1px lightblue;
	        box-shadow: 4px 4px 15px 1px lightblue;
}

.home_top_content > * {
	/* Let 1em margin space to all home_top_content children */
	margin: 1em;
}

/* Create some visual bullets on the bottom of the top background */
.home_top_bullets {
	position: absolute;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-align: center;
	    align-items: center;
}

.home_top_bullet {
	padding: 1em;
	margin: 1em;
	border-radius: 100%;
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.2)), color-stop(10%, rgba(255, 255, 255, 0.5)), color-stop(rgba(255, 255, 255, 0.7)), color-stop(90%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.2)));
	background: -webkit-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: -o-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	border: 1px solid deepskyblue;
	border-color: transparent lightyellow lightyellow transparent;
	-webkit-box-shadow: 2px 2px 15px 1px lightblue;
	        box-shadow: 2px 2px 15px 1px lightblue;
}

/* Home page bottom styles */
.home_bottom {
	max-width: 980px;
	position: relative;
	margin: 0 auto;
}

/* Create Zig-Zag borders on the sides. */
.home_bottom:before {
	background: -webkit-linear-gradient(315deg, rgba(0, 0, 0, 0.05) 16px, transparent 0), -webkit-linear-gradient(45deg, rgba(0, 0, 0, 0.05) 16px, transparent 0);
	background: -o-linear-gradient(315deg, rgba(0, 0, 0, 0.05) 16px, transparent 0), -o-linear-gradient(45deg, rgba(0, 0, 0, 0.05) 16px, transparent 0);
	background: linear-gradient(-225deg, rgba(0, 0, 0, 0.05) 16px, transparent 0), linear-gradient(45deg, rgba(0, 0, 0, 0.05) 16px, transparent 0);
	left: 0;
}

.home_bottom::after {
	background: -webkit-linear-gradient(135deg, rgba(0, 0, 0, 0.05) 16px, transparent 0), -webkit-linear-gradient(225deg, rgba(0, 0, 0, 0.05) 16px, transparent 0);
	background: -o-linear-gradient(135deg, rgba(0, 0, 0, 0.05) 16px, transparent 0), -o-linear-gradient(225deg, rgba(0, 0, 0, 0.05) 16px, transparent 0);
	background: linear-gradient(-45deg, rgba(0, 0, 0, 0.05) 16px, transparent 0), linear-gradient(225deg, rgba(0, 0, 0, 0.05) 16px, transparent 0);
	right: 0;
}

.home_bottom::after, .home_bottom::before {
	background-repeat: repeat-y;
	background-position: left top;
	background-size: 32px 32px;
	content: " ";
	display: block;
	position: absolute;
	top: 0;
	height: 100%;
	width: 32px;
}

.home_bottom_container {
	padding: 1em 28px;
}

.home_bottom_content {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-align: center;
	    align-items: center;
}

.home_bottom_content .heading {
	text-align: center;
	width: 100%;
	margin-bottom: 0.2em;
}

/* Select all the headings of home_bottom except the first one */
.home_bottom_content:not(:first-child) .heading {
	margin-top: 0.5em;
}

.home_bottom_content .icon {
	font-size: 2.5em;
	width: 1.3em; /* Fixed width for the icons */
	text-align: center;
	padding: 0.3em;
	border-radius: 0.7em;
	-webkit-box-shadow: 3px 2px 10px grey;
	        box-shadow: 3px 2px 10px grey;
}

.home_bottom_content .description {
	width: calc(100% - 5.8em);
	padding: 0 0.5em;
}

/* Small screens styles */
@media screen and (max-width: 520px) {
	.home_bottom_content .icon {
		display: none;
	}

	.home_bottom_content .description {
		width: 100%;
	}
}



